                title:
                  "Welcome to Bifrosts Tokamak Network onboarding! [EN/KR]",
                pubDate: "2021-03-26 06:11:10",
                link:
                  "https://medium.com/onther-tech/welcome-to-bifrosts-tokamak-network-onboarding-en-kr-14621c594408?source=rss----eb8117f0bafd---4",
                guid: "https://medium.com/p/14621c594408",
                author: "Onther",
                thumbnail:
                  "https://cdn-images-1.medium.com/max/1024/1*l9ic3L3BWOokPr9YtN_0yA.png",
                description:
                  '\n<figure><img alt="" src="https://cdn-images-1.medium.com/max/1024/1*l9ic3L3BWOokPr9YtN_0yA.png"></figure><p>Hi, this is an on-demand layer 2 platform, TokamakNetwork.</p>\n<p>We have recently partnered with<a href="https://thebifrost.io/">Bifrost</a>.</p>\n<h3><strong>What isBifrost?</strong></h3>\n<figure><img alt="" src="https://cdn-images-1.medium.com/max/1014/1*eYwFzwFck6e4NH1hPFqJjw.png"></figure><p>Bifrost is a multi-chain midware solution that enables developers to seamlessly use multiple blockchain protocols at the same time. Developers can adopt suitable blockchains through Bifrost, and they can be combined to expand into a novel blockchain ecosystem. In addition, it is possible to develop dApps more flexibly.</p>\n<p>In addition, Bifrost has also launched Bi-Fi, an integrated multi-chain DeFi platform based on Bifrost, which is more scalable and transparent than existing DeFi services. Bi-Fi enables trading beyond the current DeFi ecosystem through various virtual assets such as Tron and Clayton, including Bitcoin. This is because Bifrost have implemented a multi-chain environment in which multiple blockchain protocols are connected to one DApp andoperate.</p>\n<h3><strong>The purpose of the partnership</strong></h3>\n<p>The purpose of this partnership is asfollows.</p>\n<p><strong>1. Joint research on communication between layer 2, interchain, and inter blockchain</strong></p>\n<p>Interchain refers to a chain to connect different blockchains to each other. Together with Layer 2, it is one of the technologies that are expected as an alternative to solving the scalability problem of the blockchain. Tokamak Network and Bifrost plan to conduct joint research on communication technology between two different blockchains with a common goal of blockchain expansion.</p>\n<p><strong>2. Tokamak Network Layer 2 application into Bifrost based DeFi project,</strong><a href="https://bifi.finance/"><strong>Bi-Fi</strong></a></p>\n<p>Tokamak Network, an on-demand Layer 2 platform, is conducting R&amp;D to support various Layer 2 from Optimistic Rollup to Zk Rollup based on the technical understanding of Layer 2. Based on this technical background, we plan to cooperate in introducing a layer 2 solution to the DeFi platform developed by Bifrost. Through this, Bi-Fi is expected to provide better performance with lowerfees.</p>\n<p>We look forward to welcoming Bifrost as a partner of Tokamak Network, which will provide an opportunity for more people to access and utilize Layer 2 technology. In addition, we will strive to establish itself as an on-demand layer 2 platform that can support various layer 2 solutions based on our strategic partnership withBifrost.</p>\n<p>Thank you.</p>\n<p>.   2   .   <a href="https://thebifrost.io/"></a> .</p>\n<h3><strong>What isBifrost?</strong></h3>\n<figure><img alt="" src="https://cdn-images-1.medium.com/max/1014/1*eYwFzwFck6e4NH1hPFqJjw.png"></figure><p>             .        ,          .     dApp .</p>\n<p>                 .         ,       .            .</p>\n<h3><strong>The purpose of the partnership</strong></h3>\n<p>   .</p>\n<p><strong>1.  2 ,     </strong></p>\n<p>        .  2            .        ,           .</p>\n<p><strong>2.    </strong><a href="https://bifi.finance/"><strong></strong></a><strong>   2</strong></p>\n<p>    2 , 2      Optimistic Rollup Zk rollup    2     .          2    .              .</p>\n<p>       2     ,       .        2       2   .</p>\n<p>.</p>\n<img src="https://medium.com/_/stat?event=post.clientViewed&amp;referrerSource=full_rss&amp;postId=14621c594408" width="1" height="1" alt=""><hr>\n<p><a href="https://medium.com/onther-tech/welcome-to-bifrosts-tokamak-network-onboarding-en-kr-14621c594408">Welcome to Bifrosts Tokamak Network onboarding! [EN/KR]</a> was originally published in <a href="https://medium.com/onther-tech">Onther</a> on Medium, where people are continuing the conversation by highlighting and responding to this story.</p>\n',
